import React, { useState, useEffect } from 'react';
import './Navbar.css';
import "./NavbarContact"
import { NavLink, Link } from 'react-router-dom';
import NavbarContact from './NavbarContact';

function Navbar(evt) {
	const [ showHamburger, setShowHamburger ] = useState(false);
	const [ showHamurgerMenu, setShowHamurgerMenu ] = useState(false);
	const mediaMatch = window.matchMedia('(min-width: 900px)');
	const [ matches, setMatches ] = useState(mediaMatch.matches);

	useEffect(() => {
		setShowHamburger(!window.matchMedia('(min-width: 900px)').matches);
		console.log(window.matchMedia('(min-width: 900px)').matches)
	}, [])
	useEffect(() => {
		let mounting = true;
		const handler = (e) => {
			setMatches(e.matches);
			setShowHamburger(matches);
		};
		mounting?mediaMatch.addListener(handler):mediaMatch.removeListener(handler);
		
		return () => {
			mounting = false
			mediaMatch.removeListener(handler);
		}
	}, [matches])
	
	return (
		<header>
			<NavbarContact/>
			<div className="header-content">
				<div className="header-logo">
					<h2>
						<Link exact to="/">
							Hid
						</Link>
					</h2>
					<h2 className="zama">
						<Link exact to="/">
							ama
						</Link>
					</h2>
					<h2 className="began">
						<Link exact to="/">
							Began
						</Link>
					</h2>
					<Link exact to="/">
						<div class="cupping-icon-logo" />
					</Link>
				</div>
				{showHamburger ? (
					<div >
						{showHamurgerMenu ? (
							<div className="hamurger-container">
								<i onClick={() => setShowHamurgerMenu(!showHamurgerMenu)} class="fas fa-times" />
								<div className="hamurger-links-container">
									<NavLink
										exact
										className="nav-StavIslama nav-linkovi"
										activeClassName="nav-linkovi-active"
										to="/stav-islama"
									>
										Stav islama
									</NavLink>

									<NavLink
										exact
										className="nav-cjenovnik nav-linkovi"
										activeClassName="nav-linkovi-active"
										to="/cjenovnik"
									>
										Cjenovnik
									</NavLink>
								</div>
							</div>
						) : (
							<i onClick={() => setShowHamurgerMenu(!showHamurgerMenu)} class="fas fa-bars" />
						)}
					</div>
				) : (
					<nav>
						<li>
							<NavLink
								exact
								className="nav-StavIslama nav-linkovi"
								activeClassName="nav-linkovi-active"
								to="/stav-islama"
							>
								Stav islama
							</NavLink>
						</li>
						<li>
							<NavLink
								exact
								className="nav-cjenovnik nav-linkovi"
								activeClassName="nav-linkovi-active"
								to="/cjenovnik"
							>
								Cjenovnik
							</NavLink>
						</li>
					</nav>
				)}
			</div>
		</header>
	);
}

export default Navbar;
