import SiteApp from "./SiteApp";
import Admin from "./Admin";

import StavIslama from "./StavIslama";
import "./App.css";
import { SiteAppProvider } from "./Contexts/SiteApp.context";
import { RemovingProvider } from "./Contexts/Removing.Context";
import { BrowserRouter, Switch, NavLink, Route, Redirect } from "react-router-dom";

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <SiteAppProvider>
          <RemovingProvider>
            <Route exact path="/admin" render={() => <Admin />} />
            <Route path="/stav-islama" render={() => <StavIslama />} />
            <Route path="/" render={() => <SiteApp />} />
          </RemovingProvider>
        </SiteAppProvider>
      </BrowserRouter>
    </div>
  );
}

export default App;
